import React, {useState, useEffect} from "react"
import { Text, Button, SimpleGrid, Flex, Box, Image } from "@chakra-ui/core";
import * as wisun from "./../descriptions/위선자.json"
import './index.css'
import majo from '../images/results/마조히스트.jpg'

import firebase from 'firebase/app'
import 'firebase/firestore'
import 'firebase/auth'

import { useAuthState } from 'react-firebase-hooks/auth'
import { useCollectionData } from 'react-firebase-hooks/firestore'
import {config, configRealtimeDB}  from '../../initializeConfig'

import FadeIn from "react-fade-in";
import Lottie from "react-lottie";
import * as legoData from "../static/processing.json";
import * as doneData from "../static/done2.json";

import dataJson from "../static/data.json"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Transition from "../components/Transition";

// firebase.initializeApp(config)
// firebase.initializeApp(configRealtimeDB)

// const db = firebase.database()
// const firestore = firebase.firestore()
// const docRef = firestore.doc("results")
// const auth = firebase.auth()
// const firestore = firebase.firestore()

const IndexPage = () => {
	const [start , setStart] = useState(false)
	const [finish , setFinish] = useState(false)

	const [done , setDone] = useState(false)
	

	const [result , setResult] = useState('')

	const [page, setPage] = useState(0);
	const [countMap, setCountMap] = useState({})
	const [checkFuckingLoser, setCheckFuckingLoser] = useState([])

	const  order = {
		"가슴매니아" : 1 ,
		"노인성애자" : 2 ,
		"노출광" : 3 ,
		"야외플매니아" : 4 ,
		"새디스트" :5 ,
		"마조히스트" :6 ,
		"남근매니아" :7 ,
		"나르시시스트" : 8 ,
		"야동수집가" : 9 ,
		"위선자" : 10,
		default: Infinity 
  } 

  

  const onReset = () => {
	setDone(false)
	setStart(false)
	setPage(0)
	setCountMap({})
	setCheckFuckingLoser([])
	setResult('')
  }

  const dbSaveResult = (result) => {
	// docRef.set({
	// 	gender: "M",
	// 	outcome: result,
	// })
	// db.ref('/results').push({
	// 	gender: "M",
	// 	outcome: result,
	// });

	// firebase.firestore().collection("results").add({
	// 	gender: "M",
	// 	outcome: result,
	// })
  }

  const onChange = (listCount, index) => {
		if(page < 12){
			//count each value
			listCount.forEach((countName) => {
				let variable = 1
				if (countName[0] === '-'){
						variable *= -1;
						countName = countName.substring(1)
				}
					

				if(countMap[countName] !== undefined){
						setCountMap({
							...countMap,
							[countName] : (countMap[countName]+(variable))
						})
				}
				else{
						setCountMap({
							...countMap,
							[countName] : 1
						})
						
				}
			})
			setCheckFuckingLoser([...checkFuckingLoser, index])
			console.log(countMap)
			
			setPage(page+1)
			
			if( page + 1 === 12) {
				const loser = checkFuckingLoser.reduce((addedArray, item ) => {
					if(addedArray.length === 0 ) {
							addedArray.push(item)
					}
					else{
							if(addedArray[0] !== item){
								addedArray.push(item)
							}
					}
					return addedArray
				}, []).length 
				if(loser > 1){
					const counts = Object.entries(countMap);
					counts.sort((a,b) => {
							if(a[1] > b[1]) return -1
							else if(a[1] < b[1]) return 1
							else {
								return (order[a[0]] || order.default) - (order[b[0]] || order.default);
							}
					})
					setResult(counts[0][0])
					dbSaveResult(counts[0][0])
				}
				else {
					setResult('게으름뱅이')
					dbSaveResult('게으름뱅이')
				}
			}
			
		}


	}
	console.log(result)
	return (
		
		
		<Layout>
			<SEO title="Home" />
			{!start ?
				<>
					<Text fontSize="xl" my="200px"> 페티쉬 테스트에 오신걸 환영합니다 </Text>
					<Text fontSize="xl" fontFamily="MyFont">넥센 폰트 적용?</Text>
					<Text fontSize="lg"> 여러분의 페티쉬를 심리테스트로 인해 알려드리죠</Text>
					<Text fontSize="sm"> 참고로. 이 테스트는 심리학 논문을 밑받침으로 만들어진 싱빙성 있는 결과임을 알려드립니다.</Text>
					<Box fontSize="sm">오늘 참가하신 수는 5,192,184,324,987명 입니다</Box>
					<Button size="lg" my="50px" onClick={() => {setStart(!start)}}>
						자 시작해볼까요?
					</Button>
				</>
			:
				
				<>
				{
					page < 12 ?
						<>
							<Flex h="400px" alignItems="center">
								<Text
									position="absolute"
									color="#ff007640"
									fontSize="730px"
									bottom="0px"
									fontFamily="CW"
									right="-2%" > Q </Text>
								<Text fontFamily="CW" fontSize="50px" color="#d64e4ecc"> {dataJson[page].question}  </Text>
							

							</Flex>
							<SimpleGrid columns={2} spacing={5}>

							{dataJson[page].answers.map((answer, index) => {
								return (
									<Button 
										key={index}
										bg="#ff69b4" 
										fontSize="20px"
										height="150px" 
										w="150px" 
										color="white"
										borderRadius="20px"
										whiteSpace="break-spaces"
										fontFamily="CW"
										_hover={{
											backgroundColor:"#ff69b4"
										}}
										_active={{
											
											backgroundColor:"#ff69b4"
										}}
										_focus={{
											boxShadow:"0 0 0 0",
										}}
										onClick={() => {
											onChange(answer.value, index)
										}
										}>
										{answer.label}
									</Button>
								)
							})}
							</SimpleGrid>

						</>
					:
					<>
						{!done ? (
							<Transition setDone={setDone}/>
						) : (
							<>
								<Text fontSize="s" > 당신의 페티쉬는 </Text>
								<Text fontSize="xl" >{result}</Text>
								<Image src={`/src/images/results/${result}.jpg`} ></Image>
								<Text fontSize="s" >으로 판명이 났습니다</Text>

								<Image width="300px" h="300px" src={majo} />

								<Text fontSize="sm">{wisun.description}</Text>

								<Box fontSize="md">나와 같은 페티쉬를 갖고 있는 사람들은? </Box>

								<Flex>
								<Box 
									size="150px" 
									backgroundColor="white" 
									borderColor="black" 
									textAlign="center"
									borderRadius="20px" 
									fontSize="20px">극호 취향 </Box>
								<Box size="150px" 
									backgroundColor="white" 
									borderColor="black" 
									textAlign="center"
									borderRadius="20px" 
									fontSize="20px">극혐 취향 </Box>

								</Flex>
								<Box fontSize="s">부끄럽지만 공유하기</Box>
								<Button fontSize="s">부끄럽지만 공유하기</Button>

								<Button bg="#ff69b4" height="20px" w="250px" onClick={onReset} > 
									믿음이 안가서 다시 해 봐야겠다
								</Button>
								
								<Button my="20px" bg="#ff69b4" height="40px" w="250px" onClick={onReset} > 
									믿음이 안가서 다시 해 봐야겠다
								</Button>
							</>
						)}
						</>
						// <>
						// 	<Text fontSize="xl" > 결국 당신도 똑같은 남자였어 이런 붼태 {result}... (부끄부끄)</Text>
							
						// 	<Button bg="#ff69b4" height="300px" w="300px" onClick={onReset}> 
						// 	근데 나 또할래
						// 	</Button>
						// </>
				}
					
				</>
			}
			
		</Layout>
	)
}

export default IndexPage
